.title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    top: 30px;
    padding: 0 350px 0 30px;
    width: 100%;
    height: 50px;
    background: linear-gradient(115deg, #ddd3d8 56.5%, black 56.5%);
}

.title_left {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &>p {
        font-size: 28px;
        font-weight: 700;
    }

    .caught-seen {
        display: flex !important;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        width: 50%;
        
        .caught,
        .seen {
            display: flex !important;
            flex-direction: row;
            align-items: center !important;
            background: #5a5a58;
            margin-inline: 10px;
            border-radius: 10px;
            padding: 5px 20px;
            color: white;
        }
    }
}